
<template>
  <div class="log-view">
    <div class="toolbar d-flex flex-row justify-content-between">
      <div></div>
      <button class="btn btn-secondary btn-sm" v-on:click="clearLog()">Clear Log</button>
    </div>
    <div class="log">
      <pre>{{log}}</pre>
    </div>
  </div>
</template>

<script>

export default {
  name: 'LogView',
  components: {},
  data () {
    return {
      log: null,
      refreshIntervalId: null
    }
  },
  mounted () {
    this.getLog(true)
    this.refreshIntervalId = setInterval(() => {
      this.getLog()
    }, 2000)
  },
  destroyed () {
    clearInterval(this.refreshIntervalId)
  },
  methods: {
    getLog (showLoading = false) {
      const self = this

      if (showLoading) {
        self.$store.commit('setShowLoading', true)
      }

      var url = '/log'
      self.$http
        .get(url)
        .then(function (response) {
          var respObj = response.data
          if (respObj.status === 'success') {
            self.log = respObj.content
          } else {
            console.error('Failed to get log:', respObj.info)
          }
        })
        .catch(function (error) {
          self.$notify({ group: 'main', type: 'error', text: error })
        })
        .finally(function () {
          if (showLoading) {
            self.$store.commit('setShowLoading', false)
          }
        })
    },
    clearLog () {
      const self = this
      var url = '/log/clear'
      self.$http
        .get(url)
        .then(function (response) {
          var respObj = response.data
          if (respObj.status === 'success') {
            self.log = null
            self.$notify({
              group: 'main',
              type: 'success',
              text: 'Cleared!'
            })
          } else {
            self.$notify({
              group: 'main',
              type: 'error',
              text: respObj.info
            })
          }
        })
        .catch(function (error) {
          self.$notify({ group: 'main', type: 'error', text: error })
        })
    }
  }
}
</script>

