<template>
  <nav class="navbar navbar-dark bg-dark">
    <div class="container-fluid">
      <button class="btn-collapse" v-on:click="toggleLeft()">
        <svg
          class="bi bi-list"
          width="2.2em"
          height="2.2em"
          viewBox="0 0 16 16"
          fill="white"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            fill-rule="evenodd"
            d="M2.5 11.5A.5.5 0 013 11h10a.5.5 0 010 1H3a.5.5 0 01-.5-.5zm0-4A.5.5 0 013 7h10a.5.5 0 010 1H3a.5.5 0 01-.5-.5zm0-4A.5.5 0 013 3h10a.5.5 0 010 1H3a.5.5 0 01-.5-.5z"
            clip-rule="evenodd"
          />
        </svg>
      </button>
      <a class="navbar-brand" href="../">AutoTouch Dashboard</a>
      <div class="navbar-nav ml-auto">
        <button class="btn btn-secondary btn-sm" v-on:click="switchLog(!showLog)">Show Log</button>
      </div>
    </div>
  </nav>
</template>

<script>
export default {
  name: 'Navbar',
  data () {
    return {
      showLeft: true,
      showLog: false
    }
  },
  methods: {
    switchLog (show) {
      this.showLog = show
      this.$store.commit('setShowLog', show)
    },
    toggleLeft () {
      this.showLeft = !this.showLeft
      this.$store.commit('setShowLeft', this.showLeft)
    }
  }
}
</script>

